<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Material',query:{typeName,index:0}}">教学大纲</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'MaterialForm',query:{typeName,index}}">{{menuName}}</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div id="introduce" class="introduce">
          <p class="contentSmall textIndex">
            本课程在专业培养目标中定位于专业基础课，专业主干课，为必修课。本课程以动物遗传学、动物繁殖学和生物统计为基础，是猪生产学、牛生产学、禽生产学的基础，同时与数量遗传学互相促进。
          </p>
          <p class="contentSmall textIndex">
            本课程要求学生掌握家畜育种学的基本概念和原理，课程内容包括数量性状选择原理，育种值估计原理，近交与远交的遗传效应及用途，杂种优势产生的原理和利用，品种与品系培育方法。要求学生熟悉各种常见家畜品种的特征和生产力类型，熟练掌握系谱编制、综合选择指数制订、近交系数与亲缘系数制订、个体选配等必备的家畜育种措施；能根据实际生产需求设计育种方案并确定相关的育种措施。通过本课程的学习，使学生具备运用育种理论和措施解决生产中实际问题的能力。
          </p>
          <p class="contentSmall textIndex">
            本课程为54学时，其中理论学时42，实验学时12。整个理论学时划分为了3个模块，包括家畜育种学基本知识（32学时），育种新技术、新成果（6学时），引导学生自学育种知识（4学时）。
          </p>

          <h2 class="titleSmall">理论课总学时42，具体课时安排如下：</h2>
          <p class="contentSmall">
            （一）家畜育种学基本知识讲授（34学时），包括如下内容：
          </p>
          <p class="contentSmall textIndex">
            [1] 第3章 选择的作用（1学时）；
          </p>
          <p class="contentSmall textIndex">
            [2] 第4章 数量性状选择的效果与方法（6学时）
          </p>
          <p class="contentSmall textIndex">
            [3] 第5章 种畜测定（1学时）；
          </p>
          <p class="contentSmall textIndex">
            [4] 第6章 单性状育种值估计（4学时）；
          </p>
          <p class="contentSmall textIndex">
            [5] 第7章 多性状育种值估计（2学时）；
          </p>

          <p class="contentSmall">
            （二）育种新技术、新成果专题讲座（6学时），包括如下内容：
          </p>
          <p class="contentSmall textIndex">
            [1] 绪论（1学时）
          </p>
          <p class="contentSmall textIndex">
            [2] 第13章 育种新技术（3学时）
          </p>
          <p class="contentSmall textIndex">
            [3] 第14章 地方畜禽遗传资源保护和利用（2学时）
          </p>

          <p class="contentSmall">
            （三）设置课外阅读模块，指导学生查阅资料，围绕学科发展前沿问题撰写报告，进行专题讨论（4学时），包括如下内容：
          </p>
          <p class="contentSmall textIndex">
            [1] 品种（2学时）
          </p>
          <p class="contentSmall textIndex">
            [2] 家畜的性状（2学时）
          </p>

          <h2 class="titleSmall">实验课总课时12学时，内容安排如下：</h2>
          <p class="contentSmall textIndex">
            [1] 实验一：综合选择指数的制定（3学时）
          </p>
          <p class="contentSmall textIndex">
            [2] 实验二：育种值的估计；系谱的编制及近交系数的计算（3学时）
          </p>
          <p class="contentSmall textIndex">
            [3] 实验三：杂种优势及配合力的度量与分析（3学时）
          </p>
          <p class="contentSmall textIndex">
            [4] 实验四：遗传资源保护分析（3学时）
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import merge from 'webpack-merge'               //用于动态修改地址栏参数的
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        List:[],                 //评价数据数组
        menus:[],                //目录数组
        menuName:'',             //菜单名
        typeName:'Material',     //当前菜单栏选项
        index:0,                 //第几个小菜单

        file:{},                 //当前显示评价
      }
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //显示介绍
      setIntroduce(id,videoIntro){
        if(videoIntro){
          $('#'+id).html(videoIntro)
        }else{
          $('#'+id).html('此栏目下没有数据')
        }
      },

      //修改路由参数
      chanceUrlQuery(data){
        //清空所有的参数：
        this.$router.push({
          query:merge({},{})
        })
        //更新路由参数
        this.$router.push({
          query:merge(this.$route.query,data)
        })
      },

      //更新数据显示
      handleId(){
        var {List,menus,typeName,index} = this
        this.file = List[index]
        this.menuName = menus[index].menuName
        this.choiceMenuId(typeName)
        this.choiceTypeMenuId(typeName+index)
      },
    },
    watch: {
      //深度监听地址栏参数变化，一变化就切换对应的数据
      $route: {
        handler() {
          this.index = this.$route.query.index * 1
          this.handleId()
        },
        deep: true,
      }
    },
    mounted () {
      //页面重新加载时获取地址栏参数显示对应数据
      if(this.$route.query.typeName){
        this.typeName = this.$route.query.typeName
        this.index = this.$route.query.index * 1
      }else{
        var {typeName,index} = this
        this.chanceUrlQuery({typeName,index})
      }
      //当前栏目下的子菜单
      this.menus = [
        {
          typeName:'Material',
          menuName:'教学大纲',
          name:'MaterialOutLine'
        },
        {
          typeName:'Material',
          menuName:'教学目标',
          name:'MaterialDetail'
        },
        {
          typeName:'Material',
          menuName:'教学内容组织形式',
          name:'MaterialForm'
        },
      ],      //目录
      //当前栏目下的各个菜单包含的数据
      this.List = [
        {
          "id": "0",
          "courseName": "教学大纲",
          "content":'',
        },
        {
          "id": "1",
          "courseName": "教学目标",
          "content":'本课程要求学生掌握家畜育种学的基本概念和原理，包括数量性状选择原理，育种值估计原理，近交与远交的遗传效应及用途，杂种优势产生的原理和利用，品种与品系培育方法。要求学生熟悉各种常见家畜品种的特征和生产力类型，熟练掌握系谱编制、综合选择指数制订、近交系数与亲缘系数制订、个体选配等必备的家畜育种措施；能根据实际生产需求设计育种方案并确定相关的育种措施。通过本课程的学习，使学生具备运用育种理论和措施解决生产中实际问题的能力。',
        },
        {
          "id": "2",
          "courseName": "教学内容组织形式",
          "content":'',
        },
      ]
      //初始化数据
      this.handleId()
    },
  }
</script>

<style lang="less">
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          min-height: 470px;
          .introduce{
            color: #333;
            .titleSmall{
              text-align: left;
              font-weight: bold;
              font-size: 18px;
              margin: 20px 0 10px;
            }
            .textIndex{
              text-indent: 2em;
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .introduce {
              .titleSmall{
                text-indent: 0em!important;
              }
              .textIndex{
                text-indent: 1em!important;
              }
            }
          }
        }
      }
    }
  }
</style>



